.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App thead td {
  background-color: #e0e0e0;
  text-align: center;
  font-weight: bold;
}

.App td {
  text-align: left;
  padding: 4px;
  font-size: 0.8em;
}

.App table {
  width: 100%;
}

.tCenter {
  text-align: center !important;
}

.stock_0 {
  color: #909090;
}

.stock_1 {
  color: #ff297a;
}

.stock_2 {
  color: #ffab0b;
}

.stock_3 {
  color: #008000;
}

.App a:link, .App a:visited, .App a:hover, .App a:active {
  color: #505050;
}

.small {
  font-size: 0.65em;
}

.tableWrapper {
  width: 100%;
  overflow-x: auto;
}
